:root {
  --main-font: "DM Sans", sans-serif;
  --accent-font: "Titan One", cursive;
  --accent-color: #d41443;
  --light-accent-color: #ffa5ba;
  --light-color: #fff;
  --dark-color: #000;
}

.visually-hidden {
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  overflow: hidden;
  height: 1px !important;
  width: 1px !important;
  border: 0 !important;
  padding: 0 !important;
  position: absolute !important;
}

.header {
  z-index: 100;
  width: 100%;
  padding-top: 6px;
  padding-bottom: 6px;
  position: absolute;
}

.header-menu-list, .button-header {
  display: none;
}

.button-menu, .button-menu-close {
  background-color: rgba(0, 0, 0, 0);
  border: none;
  padding: 0;
}

.button-burger-icon {
  width: 20px;
  height: 12px;
  fill: var(--accent-color);
}

.menu-mobile {
  height: 100vh;
  width: 250px;
  background-color: #e17992;
  flex-direction: column;
  padding-top: 60px;
  padding-left: 21px;
  padding-right: 20px;
  display: flex;
  position: fixed;
  top: 0;
  right: 0;
  transform: translateY(-100%);
}

.menu-mobile.is-open {
  transform: translateY(0);
}

.menu-mobile-link {
  color: var(--light-color);
  font-size: 14px;
  font-weight: 700;
  line-height: 1.28;
}

.menu-mobile .menu-mobile-item:not(:last-child) {
