}

.menu-mobile .menu-mobile-item {
  border-bottom: 1px solid #e18298;
}

.header-menu-link.active, .menu-mobile-link.active {
  color: var(--accent-color);
}

.button-burger-icon-close {
  width: 20px;
  height: 12px;
  fill: var(--light-color);
  position: absolute;
  top: 23px;
  right: 28px;
}

@media (min-width: 768px) {
  .header {
    padding-top: 36px;
  }

  .button-menu {
    margin-left: auto;
